"use client";

import React, { useState, useMemo } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Label } from '@/components/ui/label';
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer, BarChart, Bar } from 'recharts';
import { useFinance } from '@/context/FinanceContext';

const AnalyticsCharts: React.FC = () => {
  const { sales, products } = useFinance();
  const [selectedProduct, setSelectedProduct] = useState<string>('all');
  const [timeRange, setTimeRange] = useState<string>('daily');

  // Process sales data for product sales chart
  const productSalesData = useMemo(() => {
    if (sales.length === 0) return [];

    const filteredSales = selectedProduct === 'all' 
      ? sales 
      : sales.filter(sale => sale.productId === selectedProduct);

    const salesByDate = filteredSales.reduce((acc, sale) => {
      const date = sale.date;
      if (!acc[date]) {
        acc[date] = { date, quantity: 0 };
      }
      acc[date].quantity += sale.quantitySold;
      return acc;
    }, {} as Record<string, { date: string; quantity: number }>);

    return Object.values(salesByDate).sort((a, b) => 
      new Date(a.date).getTime() - new Date(b.date).getTime()
    );
  }, [sales, selectedProduct]);

  // Process sales data for revenue chart
  const revenueData = useMemo(() => {
    if (sales.length === 0) return [];

    const revenueByDate = sales.reduce((acc, sale) => {
      let dateKey = sale.date;
      
      // Group by time range
      if (timeRange === 'weekly') {
        const date = new Date(sale.date);
        const startOfWeek = new Date(date.setDate(date.getDate() - date.getDay()));
        dateKey = startOfWeek.toISOString().split('T')[0];
      } else if (timeRange === 'monthly') {
        const date = new Date(sale.date);
        dateKey = `${date.getFullYear()}-${String(date.getMonth() + 1).padStart(2, '0')}`;
      }

      if (!acc[dateKey]) {
        acc[dateKey] = { date: dateKey, revenue: 0 };
      }
      acc[dateKey].revenue += sale.amountReceived;
      return acc;
    }, {} as Record<string, { date: string; revenue: number }>);

    return Object.values(revenueByDate).sort((a, b) => 
      new Date(a.date).getTime() - new Date(b.date).getTime()
    );
  }, [sales, timeRange]);

  // Calculate summary statistics
  const totalRevenue = sales.reduce((sum, sale) => sum + sale.amountReceived, 0);
  const totalQuantitySold = sales.reduce((sum, sale) => sum + sale.quantitySold, 0);
  const averageSaleValue = sales.length > 0 ? totalRevenue / sales.length : 0;
  
  // Top selling products
  const topProducts = useMemo(() => {
    const productSales = sales.reduce((acc, sale) => {
      if (!acc[sale.productId]) {
        acc[sale.productId] = {
          productName: sale.productName,
          totalQuantity: 0,
          totalRevenue: 0
        };
      }
      acc[sale.productId].totalQuantity += sale.quantitySold;
      acc[sale.productId].totalRevenue += sale.amountReceived;
      return acc;
    }, {} as Record<string, { productName: string; totalQuantity: number; totalRevenue: number }>);

    return Object.values(productSales)
      .sort((a, b) => b.totalQuantity - a.totalQuantity)
      .slice(0, 5);
  }, [sales]);

  const formatDate = (dateStr: string) => {
    const date = new Date(dateStr);
    if (timeRange === 'monthly') {
      return date.toLocaleDateString('en-US', { year: 'numeric', month: 'short' });
    }
    return date.toLocaleDateString('en-US', { month: 'short', day: 'numeric' });
  };

  const CustomTooltip = ({ active, payload, label }: any) => {
    if (active && payload && payload.length) {
      return (
        <div className="bg-white p-3 border rounded-lg shadow-lg">
          <p className="font-medium">{formatDate(label)}</p>
          {payload.map((entry: any, index: number) => (
            <p key={index} style={{ color: entry.color }}>
              {entry.dataKey === 'quantity' ? 'Quantity: ' : 'Revenue: $'}
              {entry.dataKey === 'revenue' 
                ? entry.value.toLocaleString() 
                : entry.value
              }
            </p>
          ))}
        </div>
      );
    }
    return null;
  };

  return (
    <div className="space-y-6">
      {/* Summary Statistics */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium text-gray-600">
              Total Revenue
            </CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-green-600">
              ${totalRevenue.toLocaleString()}
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium text-gray-600">
              Items Sold
            </CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-blue-600">
              {totalQuantitySold.toLocaleString()}
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium text-gray-600">
              Average Sale Value
            </CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-purple-600">
              ${averageSaleValue.toFixed(2)}
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Charts */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Product Sales Chart */}
        <Card>
          <CardHeader>
            <CardTitle>Product Sales Over Time</CardTitle>
            <div className="space-y-2">
              <Label htmlFor="product-select">Filter by Product:</Label>
              <Select value={selectedProduct} onValueChange={setSelectedProduct}>
                <SelectTrigger id="product-select">
                  <SelectValue placeholder="Select product" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="all">All Products</SelectItem>
                  {products.map((product) => (
                    <SelectItem key={product.id} value={product.id}>
                      {product.name}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>
          </CardHeader>
          <CardContent>
            {productSalesData.length === 0 ? (
              <div className="flex items-center justify-center h-64 text-gray-500">
                No sales data available
              </div>
            ) : (
              <ResponsiveContainer width="100%" height={300}>
                <LineChart data={productSalesData}>
                  <CartesianGrid strokeDasharray="3 3" />
                  <XAxis 
                    dataKey="date" 
                    tickFormatter={formatDate}
                  />
                  <YAxis />
                  <Tooltip content={<CustomTooltip />} />
                  <Line 
                    type="monotone" 
                    dataKey="quantity" 
                    stroke="#3b82f6" 
                    strokeWidth={2}
                    dot={{ fill: '#3b82f6' }}
                  />
                </LineChart>
              </ResponsiveContainer>
            )}
          </CardContent>
        </Card>

        {/* Revenue Chart */}
        <Card>
          <CardHeader>
            <CardTitle>Revenue Over Time</CardTitle>
            <div className="space-y-2">
              <Label htmlFor="time-range-select">Time Range:</Label>
              <Select value={timeRange} onValueChange={setTimeRange}>
                <SelectTrigger id="time-range-select">
                  <SelectValue placeholder="Select time range" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="daily">Daily</SelectItem>
                  <SelectItem value="weekly">Weekly</SelectItem>
                  <SelectItem value="monthly">Monthly</SelectItem>
                </SelectContent>
              </Select>
            </div>
          </CardHeader>
          <CardContent>
            {revenueData.length === 0 ? (
              <div className="flex items-center justify-center h-64 text-gray-500">
                No revenue data available
              </div>
            ) : (
              <ResponsiveContainer width="100%" height={300}>
                <BarChart data={revenueData}>
                  <CartesianGrid strokeDasharray="3 3" />
                  <XAxis 
                    dataKey="date" 
                    tickFormatter={formatDate}
                  />
                  <YAxis />
                  <Tooltip content={<CustomTooltip />} />
                  <Bar 
                    dataKey="revenue" 
                    fill="#10b981"
                  />
                </BarChart>
              </ResponsiveContainer>
            )}
          </CardContent>
        </Card>
      </div>

      {/* Top Selling Products */}
      {topProducts.length > 0 && (
        <Card>
          <CardHeader>
            <CardTitle>Top Selling Products</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              {topProducts.map((product, index) => (
                <div key={index} className="flex justify-between items-center p-3 bg-gray-50 rounded-lg">
                  <div>
                    <h4 className="font-semibold">{product.productName}</h4>
                    <p className="text-sm text-gray-600">
                      {product.totalQuantity} units sold
                    </p>
                  </div>
                  <div className="text-right">
                    <p className="font-semibold text-green-600">
                      ${product.totalRevenue.toLocaleString()}
                    </p>
                    <p className="text-sm text-gray-600">Revenue</p>
                  </div>
                </div>
              ))}
            </div>
          </CardContent>
        </Card>
      )}
    </div>
  );
};

export default AnalyticsCharts;
