"use client";

import React, { useState } from 'react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { useFinance, Sale } from '@/context/FinanceContext';

const SalesRecords: React.FC = () => {
  const { sales, products, addSale, updateSale, deleteSale } = useFinance();
  const [isDialogOpen, setIsDialogOpen] = useState(false);
  const [editingSale, setEditingSale] = useState<Sale | null>(null);
  const [formData, setFormData] = useState({
    productId: '',
    quantitySold: '',
    date: '',
    amountReceived: ''
  });
  const [error, setError] = useState('');

  const resetForm = () => {
    setFormData({
      productId: '',
      quantitySold: '',
      date: new Date().toISOString().split('T')[0],
      amountReceived: ''
    });
    setEditingSale(null);
    setError('');
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    setError('');

    if (!formData.productId) {
      setError('Please select a product');
      return;
    }

    const quantitySold = parseInt(formData.quantitySold);
    if (isNaN(quantitySold) || quantitySold <= 0) {
      setError('Please enter a valid quantity');
      return;
    }

    const amountReceived = parseFloat(formData.amountReceived);
    if (isNaN(amountReceived) || amountReceived < 0) {
      setError('Please enter a valid amount');
      return;
    }

    const selectedProduct = products.find(p => p.id === formData.productId);
    if (!selectedProduct) {
      setError('Selected product not found');
      return;
    }

    if (!editingSale && quantitySold > selectedProduct.quantity) {
      setError(`Not enough stock. Available: ${selectedProduct.quantity}`);
      return;
    }

    try {
      if (editingSale) {
        updateSale(editingSale.id, {
          productId: formData.productId,
          productName: selectedProduct.name,
          quantitySold,
          date: formData.date || new Date().toISOString().split('T')[0],
          amountReceived
        });
      } else {
        addSale({
          productId: formData.productId,
          productName: selectedProduct.name,
          quantitySold,
          date: formData.date || new Date().toISOString().split('T')[0],
          amountReceived
        });
      }
      
      resetForm();
      setIsDialogOpen(false);
    } catch (err) {
      setError('Failed to save sale record');
    }
  };

  const handleEdit = (sale: Sale) => {
    setEditingSale(sale);
    setFormData({
      productId: sale.productId,
      quantitySold: sale.quantitySold.toString(),
      date: sale.date,
      amountReceived: sale.amountReceived.toString()
    });
    setIsDialogOpen(true);
  };

  const handleDelete = (id: string) => {
    if (confirm('Are you sure you want to delete this sale record? This will restore the product quantity.')) {
      deleteSale(id);
    }
  };

  const handleDialogClose = () => {
    setIsDialogOpen(false);
    resetForm();
  };

  const handleProductChange = (productId: string) => {
    setFormData({ ...formData, productId });
    
    // Auto-calculate amount based on product price and quantity
    const selectedProduct = products.find(p => p.id === productId);
    if (selectedProduct && formData.quantitySold) {
      const quantity = parseInt(formData.quantitySold);
      if (!isNaN(quantity)) {
        setFormData(prev => ({
          ...prev,
          productId,
          amountReceived: (selectedProduct.price * quantity).toString()
        }));
      }
    }
  };

  const handleQuantityChange = (quantity: string) => {
    setFormData({ ...formData, quantitySold: quantity });
    
    // Auto-calculate amount based on product price and quantity
    if (formData.productId && quantity) {
      const selectedProduct = products.find(p => p.id === formData.productId);
      if (selectedProduct) {
        const qty = parseInt(quantity);
        if (!isNaN(qty)) {
          setFormData(prev => ({
            ...prev,
            quantitySold: quantity,
            amountReceived: (selectedProduct.price * qty).toString()
          }));
        }
      }
    }
  };

  const totalRevenue = sales.reduce((sum, sale) => sum + sale.amountReceived, 0);
  const totalQuantitySold = sales.reduce((sum, sale) => sum + sale.quantitySold, 0);

  return (
    <div className="space-y-6">
      {/* Header with Stats */}
      <div className="flex justify-between items-center">
        <div>
          <h3 className="text-lg font-medium text-gray-900">
            Sales Records ({sales.length})
          </h3>
          <div className="flex space-x-4 text-sm text-gray-600 mt-1">
            <span>Total Revenue: <span className="font-semibold text-green-600">${totalRevenue.toLocaleString()}</span></span>
            <span>Total Items Sold: <span className="font-semibold">{totalQuantitySold}</span></span>
          </div>
        </div>
        <Dialog open={isDialogOpen} onOpenChange={setIsDialogOpen}>
          <DialogTrigger asChild>
            <Button onClick={() => setIsDialogOpen(true)} disabled={products.length === 0}>
              Record New Sale
            </Button>
          </DialogTrigger>
          <DialogContent>
            <DialogHeader>
              <DialogTitle>
                {editingSale ? 'Edit Sale Record' : 'Record New Sale'}
              </DialogTitle>
            </DialogHeader>
            <form onSubmit={handleSubmit} className="space-y-4">
              <div className="space-y-2">
                <Label htmlFor="product">Product</Label>
                <Select value={formData.productId} onValueChange={handleProductChange}>
                  <SelectTrigger>
                    <SelectValue placeholder="Select a product" />
                  </SelectTrigger>
                  <SelectContent>
                    {products.map((product) => (
                      <SelectItem key={product.id} value={product.id}>
                        {product.name} - ${product.price} (Stock: {product.quantity})
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>
              
              <div className="space-y-2">
                <Label htmlFor="quantitySold">Quantity Sold</Label>
                <Input
                  id="quantitySold"
                  type="number"
                  min="1"
                  value={formData.quantitySold}
                  onChange={(e) => handleQuantityChange(e.target.value)}
                  placeholder="Enter quantity"
                  required
                />
              </div>
              
              <div className="space-y-2">
                <Label htmlFor="date">Sale Date</Label>
                <Input
                  id="date"
                  type="date"
                  value={formData.date}
                  onChange={(e) => setFormData({ ...formData, date: e.target.value })}
                  required
                />
              </div>
              
              <div className="space-y-2">
                <Label htmlFor="amountReceived">Amount Received ($)</Label>
                <Input
                  id="amountReceived"
                  type="number"
                  step="0.01"
                  min="0"
                  value={formData.amountReceived}
                  onChange={(e) => setFormData({ ...formData, amountReceived: e.target.value })}
                  placeholder="0.00"
                  required
                />
              </div>
              
              {error && (
                <Alert variant="destructive">
                  <AlertDescription>{error}</AlertDescription>
                </Alert>
              )}
              
              <div className="flex justify-end space-x-2">
                <Button type="button" variant="outline" onClick={handleDialogClose}>
                  Cancel
                </Button>
                <Button type="submit">
                  {editingSale ? 'Update' : 'Record'} Sale
                </Button>
              </div>
            </form>
          </DialogContent>
        </Dialog>
      </div>

      {/* No Products Warning */}
      {products.length === 0 && (
        <Alert>
          <AlertDescription>
            You need to add products to your inventory before recording sales.
          </AlertDescription>
        </Alert>
      )}

      {/* Sales List */}
      {sales.length === 0 ? (
        <Card>
          <CardContent className="flex items-center justify-center h-32">
            <p className="text-gray-500">
              {products.length === 0 
                ? "Add products to your inventory first, then record sales here."
                : "No sales recorded yet. Record your first sale to get started."
              }
            </p>
          </CardContent>
        </Card>
      ) : (
        <div className="grid gap-4">
          {sales
            .sort((a, b) => new Date(b.date).getTime() - new Date(a.date).getTime())
            .map((sale) => (
            <Card key={sale.id}>
              <CardContent className="p-4">
                <div className="flex justify-between items-start">
                  <div className="flex-1">
                    <h4 className="font-semibold text-gray-900">{sale.productName}</h4>
                    <div className="mt-2 grid grid-cols-2 md:grid-cols-4 gap-4 text-sm">
                      <div>
                        <span className="text-gray-600">Quantity:</span>
                        <p className="font-semibold">{sale.quantitySold}</p>
                      </div>
                      <div>
                        <span className="text-gray-600">Amount:</span>
                        <p className="font-semibold text-green-600">${sale.amountReceived.toLocaleString()}</p>
                      </div>
                      <div>
                        <span className="text-gray-600">Date:</span>
                        <p className="font-semibold">{new Date(sale.date).toLocaleDateString()}</p>
                      </div>
                      <div>
                        <span className="text-gray-600">Unit Price:</span>
                        <p className="font-semibold">${(sale.amountReceived / sale.quantitySold).toFixed(2)}</p>
                      </div>
                    </div>
                  </div>
                  <div className="flex space-x-2 ml-4">
                    <Button
                      variant="outline"
                      size="sm"
                      onClick={() => handleEdit(sale)}
                    >
                      Edit
                    </Button>
                    <Button
                      variant="destructive"
                      size="sm"
                      onClick={() => handleDelete(sale.id)}
                    >
                      Delete
                    </Button>
                  </div>
                </div>
              </CardContent>
            </Card>
          ))}
        </div>
      )}
    </div>
  );
};

export default SalesRecords;
