"use client";

import React, { createContext, useContext, useState, useEffect } from 'react';

// Data interfaces
export interface Creditor {
  id: string;
  name: string;
  amountOwed: number;
  lastPaymentDate: string;
}

export interface Debtor {
  id: string;
  name: string;
  amountOwed: number;
  lastPaymentDate: string;
}

export interface Product {
  id: string;
  name: string;
  category: string;
  price: number;
  quantity: number;
}

export interface Sale {
  id: string;
  productId: string;
  productName: string;
  quantitySold: number;
  date: string;
  amountReceived: number;
}

export interface Payment {
  id: string;
  entity: string;
  entityType: 'creditor' | 'debtor';
  amount: number;
  date: string;
  type: 'payment' | 'received';
}

interface FinanceContextType {
  // Data
  creditors: Creditor[];
  debtors: Debtor[];
  products: Product[];
  sales: Sale[];
  payments: Payment[];
  
  // Creditor operations
  addCreditor: (creditor: Omit<Creditor, 'id'>) => void;
  updateCreditor: (id: string, creditor: Partial<Creditor>) => void;
  deleteCreditor: (id: string) => void;
  
  // Debtor operations
  addDebtor: (debtor: Omit<Debtor, 'id'>) => void;
  updateDebtor: (id: string, debtor: Partial<Debtor>) => void;
  deleteDebtor: (id: string) => void;
  
  // Product operations
  addProduct: (product: Omit<Product, 'id'>) => void;
  updateProduct: (id: string, product: Partial<Product>) => void;
  deleteProduct: (id: string) => void;
  
  // Sale operations
  addSale: (sale: Omit<Sale, 'id'>) => void;
  updateSale: (id: string, sale: Partial<Sale>) => void;
  deleteSale: (id: string) => void;
  
  // Payment operations
  addPayment: (payment: Omit<Payment, 'id'>) => void;
  deletePayment: (id: string) => void;
}

const FinanceContext = createContext<FinanceContextType | undefined>(undefined);

export const useFinance = () => {
  const context = useContext(FinanceContext);
  if (!context) {
    throw new Error('useFinance must be used within a FinanceProvider');
  }
  return context;
};

export const FinanceProvider: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  const [creditors, setCreditors] = useState<Creditor[]>([]);
  const [debtors, setDebtors] = useState<Debtor[]>([]);
  const [products, setProducts] = useState<Product[]>([]);
  const [sales, setSales] = useState<Sale[]>([]);
  const [payments, setPayments] = useState<Payment[]>([]);

  // Load data from localStorage on mount
  useEffect(() => {
    const savedCreditors = localStorage.getItem('finance-creditors');
    const savedDebtors = localStorage.getItem('finance-debtors');
    const savedProducts = localStorage.getItem('finance-products');
    const savedSales = localStorage.getItem('finance-sales');
    const savedPayments = localStorage.getItem('finance-payments');

    if (savedCreditors) setCreditors(JSON.parse(savedCreditors));
    if (savedDebtors) setDebtors(JSON.parse(savedDebtors));
    if (savedProducts) setProducts(JSON.parse(savedProducts));
    if (savedSales) setSales(JSON.parse(savedSales));
    if (savedPayments) setPayments(JSON.parse(savedPayments));
  }, []);

  // Save to localStorage whenever data changes
  useEffect(() => {
    localStorage.setItem('finance-creditors', JSON.stringify(creditors));
  }, [creditors]);

  useEffect(() => {
    localStorage.setItem('finance-debtors', JSON.stringify(debtors));
  }, [debtors]);

  useEffect(() => {
    localStorage.setItem('finance-products', JSON.stringify(products));
  }, [products]);

  useEffect(() => {
    localStorage.setItem('finance-sales', JSON.stringify(sales));
  }, [sales]);

  useEffect(() => {
    localStorage.setItem('finance-payments', JSON.stringify(payments));
  }, [payments]);

  // Utility function to generate IDs
  const generateId = () => Date.now().toString() + Math.random().toString(36).substr(2, 9);

  // Creditor operations
  const addCreditor = (creditor: Omit<Creditor, 'id'>) => {
    const newCreditor = { ...creditor, id: generateId() };
    setCreditors(prev => [...prev, newCreditor]);
  };

  const updateCreditor = (id: string, creditor: Partial<Creditor>) => {
    setCreditors(prev => prev.map(c => c.id === id ? { ...c, ...creditor } : c));
  };

  const deleteCreditor = (id: string) => {
    setCreditors(prev => prev.filter(c => c.id !== id));
  };

  // Debtor operations
  const addDebtor = (debtor: Omit<Debtor, 'id'>) => {
    const newDebtor = { ...debtor, id: generateId() };
    setDebtors(prev => [...prev, newDebtor]);
  };

  const updateDebtor = (id: string, debtor: Partial<Debtor>) => {
    setDebtors(prev => prev.map(d => d.id === id ? { ...d, ...debtor } : d));
  };

  const deleteDebtor = (id: string) => {
    setDebtors(prev => prev.filter(d => d.id !== id));
  };

  // Product operations
  const addProduct = (product: Omit<Product, 'id'>) => {
    const newProduct = { ...product, id: generateId() };
    setProducts(prev => [...prev, newProduct]);
  };

  const updateProduct = (id: string, product: Partial<Product>) => {
    setProducts(prev => prev.map(p => p.id === id ? { ...p, ...product } : p));
  };

  const deleteProduct = (id: string) => {
    setProducts(prev => prev.filter(p => p.id !== id));
    // Also remove related sales
    setSales(prev => prev.filter(s => s.productId !== id));
  };

  // Sale operations
  const addSale = (sale: Omit<Sale, 'id'>) => {
    const newSale = { ...sale, id: generateId() };
    setSales(prev => [...prev, newSale]);
    
    // Update product quantity
    setProducts(prev => prev.map(p => 
      p.id === sale.productId 
        ? { ...p, quantity: Math.max(0, p.quantity - sale.quantitySold) }
        : p
    ));
  };

  const updateSale = (id: string, sale: Partial<Sale>) => {
    setSales(prev => prev.map(s => s.id === id ? { ...s, ...sale } : s));
  };

  const deleteSale = (id: string) => {
    const saleToDelete = sales.find(s => s.id === id);
    if (saleToDelete) {
      // Restore product quantity
      setProducts(prev => prev.map(p => 
        p.id === saleToDelete.productId 
          ? { ...p, quantity: p.quantity + saleToDelete.quantitySold }
          : p
      ));
    }
    setSales(prev => prev.filter(s => s.id !== id));
  };

  // Payment operations
  const addPayment = (payment: Omit<Payment, 'id'>) => {
    const newPayment = { ...payment, id: generateId() };
    setPayments(prev => [...prev, newPayment]);
  };

  const deletePayment = (id: string) => {
    setPayments(prev => prev.filter(p => p.id !== id));
  };

  const value: FinanceContextType = {
    creditors,
    debtors,
    products,
    sales,
    payments,
    addCreditor,
    updateCreditor,
    deleteCreditor,
    addDebtor,
    updateDebtor,
    deleteDebtor,
    addProduct,
    updateProduct,
    deleteProduct,
    addSale,
    updateSale,
    deleteSale,
    addPayment,
    deletePayment,
  };

  return (
    <FinanceContext.Provider value={value}>
      {children}
    </FinanceContext.Provider>
  );
};
