"use client";

import React, { useState } from 'react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog';
import { useFinance, Creditor } from '@/context/FinanceContext';

const CreditorManagement: React.FC = () => {
  const { creditors, addCreditor, updateCreditor, deleteCreditor } = useFinance();
  const [isDialogOpen, setIsDialogOpen] = useState(false);
  const [editingCreditor, setEditingCreditor] = useState<Creditor | null>(null);
  const [formData, setFormData] = useState({
    name: '',
    amountOwed: '',
    lastPaymentDate: ''
  });
  const [error, setError] = useState('');

  const resetForm = () => {
    setFormData({
      name: '',
      amountOwed: '',
      lastPaymentDate: ''
    });
    setEditingCreditor(null);
    setError('');
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    setError('');

    if (!formData.name.trim()) {
      setError('Name is required');
      return;
    }

    const amountOwed = parseFloat(formData.amountOwed);
    if (isNaN(amountOwed) || amountOwed < 0) {
      setError('Please enter a valid amount');
      return;
    }

    try {
      if (editingCreditor) {
        updateCreditor(editingCreditor.id, {
          name: formData.name.trim(),
          amountOwed,
          lastPaymentDate: formData.lastPaymentDate || new Date().toISOString().split('T')[0]
        });
      } else {
        addCreditor({
          name: formData.name.trim(),
          amountOwed,
          lastPaymentDate: formData.lastPaymentDate || new Date().toISOString().split('T')[0]
        });
      }
      
      resetForm();
      setIsDialogOpen(false);
    } catch (err) {
      setError('Failed to save creditor');
    }
  };

  const handleEdit = (creditor: Creditor) => {
    setEditingCreditor(creditor);
    setFormData({
      name: creditor.name,
      amountOwed: creditor.amountOwed.toString(),
      lastPaymentDate: creditor.lastPaymentDate
    });
    setIsDialogOpen(true);
  };

  const handleDelete = (id: string) => {
    if (confirm('Are you sure you want to delete this creditor?')) {
      deleteCreditor(id);
    }
  };

  const handleDialogClose = () => {
    setIsDialogOpen(false);
    resetForm();
  };

  return (
    <div className="space-y-6">
      {/* Add New Creditor Button */}
      <div className="flex justify-between items-center">
        <h3 className="text-lg font-medium text-gray-900">
          Creditors ({creditors.length})
        </h3>
        <Dialog open={isDialogOpen} onOpenChange={setIsDialogOpen}>
          <DialogTrigger asChild>
            <Button onClick={() => setIsDialogOpen(true)}>
              Add New Creditor
            </Button>
          </DialogTrigger>
          <DialogContent>
            <DialogHeader>
              <DialogTitle>
                {editingCreditor ? 'Edit Creditor' : 'Add New Creditor'}
              </DialogTitle>
            </DialogHeader>
            <form onSubmit={handleSubmit} className="space-y-4">
              <div className="space-y-2">
                <Label htmlFor="name">Name</Label>
                <Input
                  id="name"
                  value={formData.name}
                  onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                  placeholder="Enter creditor name"
                  required
                />
              </div>
              
              <div className="space-y-2">
                <Label htmlFor="amountOwed">Amount Owed ($)</Label>
                <Input
                  id="amountOwed"
                  type="number"
                  step="0.01"
                  min="0"
                  value={formData.amountOwed}
                  onChange={(e) => setFormData({ ...formData, amountOwed: e.target.value })}
                  placeholder="0.00"
                  required
                />
              </div>
              
              <div className="space-y-2">
                <Label htmlFor="lastPaymentDate">Last Payment Date</Label>
                <Input
                  id="lastPaymentDate"
                  type="date"
                  value={formData.lastPaymentDate}
                  onChange={(e) => setFormData({ ...formData, lastPaymentDate: e.target.value })}
                />
              </div>
              
              {error && (
                <Alert variant="destructive">
                  <AlertDescription>{error}</AlertDescription>
                </Alert>
              )}
              
              <div className="flex justify-end space-x-2">
                <Button type="button" variant="outline" onClick={handleDialogClose}>
                  Cancel
                </Button>
                <Button type="submit">
                  {editingCreditor ? 'Update' : 'Add'} Creditor
                </Button>
              </div>
            </form>
          </DialogContent>
        </Dialog>
      </div>

      {/* Creditors List */}
      {creditors.length === 0 ? (
        <Card>
          <CardContent className="flex items-center justify-center h-32">
            <p className="text-gray-500">No creditors found. Add your first creditor to get started.</p>
          </CardContent>
        </Card>
      ) : (
        <div className="grid gap-4">
          {creditors.map((creditor) => (
            <Card key={creditor.id}>
              <CardContent className="p-4">
                <div className="flex justify-between items-start">
                  <div className="flex-1">
                    <h4 className="font-semibold text-gray-900">{creditor.name}</h4>
                    <div className="mt-2 space-y-1">
                      <p className="text-sm text-gray-600">
                        <span className="font-medium">Amount Owed:</span>{' '}
                        <span className="text-red-600 font-semibold">
                          ${creditor.amountOwed.toLocaleString()}
                        </span>
                      </p>
                      <p className="text-sm text-gray-600">
                        <span className="font-medium">Last Payment:</span>{' '}
                        {new Date(creditor.lastPaymentDate).toLocaleDateString()}
                      </p>
                    </div>
                  </div>
                  <div className="flex space-x-2">
                    <Button
                      variant="outline"
                      size="sm"
                      onClick={() => handleEdit(creditor)}
                    >
                      Edit
                    </Button>
                    <Button
                      variant="destructive"
                      size="sm"
                      onClick={() => handleDelete(creditor.id)}
                    >
                      Delete
                    </Button>
                  </div>
                </div>
              </CardContent>
            </Card>
          ))}
        </div>
      )}
    </div>
  );
};

export default CreditorManagement;
